import React from "react";

import { FirstStory } from "./FirstStory";
import "./css/TwoStory.css";
export const TwoStory = (props) => {
  console.log("Two Story Component");
  console.log(props.data);
  const slug1 = props.data[0].slug;
  const name1 = props.data[0].name;
  const slug2 = props.data[1].slug;
  const name2 = props.data[1].name;
  console.log(slug1 + " " + slug2);
  return (
    <div class="second-group-collection">
      <div className="entertainment-grid">
        <FirstStory data={slug1} name={name1}></FirstStory>
        <FirstStory data={slug2} name={name2}></FirstStory>
      </div>
    </div>
  );
};
