import React from "react";

export const StoryCard = (props) => {
  if (props.type === "blurb") {
    return (
      <>
        <div className="story-element-text-blurb">
          <div dangerouslySetInnerHTML={{ __html: props.content }}></div>
        </div>
      </>
    );
  }
  if (props.type === "also-read") {
    return (
      <>
        <div className="_1a5aS">
          <div class="_1Jj4B">Also Read</div>
          <div className="_2FuPS">
            <h1 className="headline-type--17">{props.content}</h1>
          </div>
        </div>
      </>
    );
  }
  return (
    <>
      <div className="story-element-text">
        <div dangerouslySetInnerHTML={{ __html: props.content }}></div>
      </div>
    </>
  );
};
